import "./card.css";

export const createCard = () => {
  const card = document.createElement("div");
  card.classList = "card";

  const cardImg = document.createElement("img");
  cardImg.src =
    "https://www.veeforu.com/wp-content/uploads/2023/03/Purple-cyan-gradient-Youtube-thumbnail-background-png-1024x576.jpg";
  cardImg.classList = "card-img";

  const cardBody = document.createElement("h1");
  cardBody.classList = "card-body";
  cardBody.textContent = "Kleb Jeb";

  const cardRate = document.createElement("div");
  cardRate.classList = "card-rate";
  cardRate.textContent = "* * * * *";

  card.append(cardImg);
  card.append(cardBody);
  card.append(cardRate);

  return card;
};
